/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import portfolioSummaryData from './data-bb-portfolio-summary-http';

const portfolioSummaryDataModuleKey = 'data-bb-portfolio-summary-http-ng';

export const portfolioSummaryDataKey = 'data-bb-portfolio-summary-http-ng:portfolioSummaryData';

export default ng
  .module(portfolioSummaryDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(portfolioSummaryDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       portfolioSummaryData(config),
      ],
    };
  }])
  .name;



